import React, { Component } from "react";
import Slider from "react-slick";
import Vivus from "vivus";
import "./Home.scss";
import styles from "./Home.module.scss";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from "axios";
import PlayCircleOutlineIcon from "@material-ui/icons/PlayCircleOutline";
import InfoIcon from "@material-ui/icons/Info";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import WhatshotIcon from "@material-ui/icons/Whatshot";
import FilterListIcon from "@material-ui/icons/FilterList";
import BuildIcon from "@material-ui/icons/Build";
import Alertm from "./Alertm.js";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import AccountCircle from "@material-ui/icons/AccountCircle";
import PhoneIcon from "@material-ui/icons/Phone";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import SubjectIcon from "@material-ui/icons/Subject";
import ListIcon from "@material-ui/icons/List";
import Tooltip from "@material-ui/core/Tooltip";
import Select from "react-select";
import ReactHtmlParser from "react-html-parser";

const customStyles = {
  placeholder: () => ({
    padding: "10px",
    backgroundColor: styles.c11,
    borderTopRightRadius: 5,
    color: styles.c13,
  }),
  menu: () => ({
    backgroundColor: styles.c11,
    textAlign: "center",
    color: styles.c3,
  }),
  option: () => ({
    fontFamily: styles.font3,
    cursor: "pointer",
  }),
  singleValue: () => ({
    color: styles.c3,
    padding: "10px",
    backgroundColor: styles.c11,
    borderTopRightRadius: 5,
  }),
  control: () => ({
    width: "fit-content",
    color: styles.c3,
    fontFamily: styles.font3,
    fontWeight: 500,
    fontSize: "1.2em",
    marginLeft: "2%",
    marginBottom: "10px",
  }),
  input: () => ({
    color: "transparent",
  }),
};

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      settings: {
        dots: false,
        infinite: true,
        autoplaySpeed: 15000,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        autoplay: true,
        speed: 500,
        pauseOnHover: false,
        fade: true,
        beforeChange: this.vidtoggle,
      },
      w: window.scrollTo(0, 0),
      disp: { display: "none" },
      name: null,
      email: null,
      phone: null,
      selectedOption: null,
      text: null,
      a: null,
      res: {},
      vi: null,
      count: 1,
      svg: null,
      load: true,
      src0: "",
      src1: "",
      wi: "",
      hi: "",
    };
  }
  componentDidMount() {
    this.resolution();
    axios.post('/hom').then((res) => {
      this.setState({
        res: res.data,
        load: false,
        vi: res.data.vid.map((key, index) => {
          const d = index === 0 ?  "inline-block": "";
          return (
            <React.Fragment key={index}>
              <link rel="preload" href={key[0]} as="image" />
              <span
                className={ReactHtmlParser("videoLink " + d + " d" + [index + 1])}
                onClick={() => this.vid(key[1])}
              >
                {ReactHtmlParser(key[2])}{" "}
                <PlayCircleOutlineIcon />
              </span>
              <img
                className={ReactHtmlParser("videoImg " + d + " d" + [index + 1])}
                src={key[0]}
                alt={ReactHtmlParser(key[2])}
                width="275"
                height="275"
              />
              <link rel="preload" href={key[3]} as="image" />
              <span
                className={ReactHtmlParser("videoLink " + d + " d" + [index + 1])}
                onClick={() => this.vid(key[4])}
              >
                {ReactHtmlParser(key[5])}{" "}
                <PlayCircleOutlineIcon />
              </span>
              <img
                className={ReactHtmlParser("videoImg " + d + " d" + [index + 1])}
                src={key[3]}
                alt={ReactHtmlParser(key[5])}
                width="275"
                height="275"
              />
            </React.Fragment>
          );
        }),
      });
      document.getElementById("my-svg").innerHTML = this.state.res.svg;
      new Vivus("my-svg", { duration: 200 });
      if (window.location.search === "?in=in") this.scr();
    })
      .catch((error) => {
        alert(error);
      });
  }
  resolution = () => {
    let width = window.screen.width;
    width > 1200 ?
      this.setState({
        src0: "https://candid.s3-ap-southeast-2.amazonaws.com/wel1.jpg",
        src1: "https://candid.s3-ap-southeast-2.amazonaws.com/wel2.jpg",
      })
      :
      this.setState({
        src0: "https://candid.s3-ap-southeast-2.amazonaws.com/wel1m.jpg",
        src1: "https://candid.s3-ap-southeast-2.amazonaws.com/wel2m.jpg",
      });
    if (width > 1200) return this.setState({
      wi: "1200",
      hi: "485",
    });
    if (width <= 1200 && width >= 576) return this.setState({
      wi: "992",
      hi: "410",
    });
    if (width < 576) this.setState({
      wi: "360",
      hi: "146",
    });
  };
  scr = () => {
    document.getElementById("enquiry").scrollIntoView();
  };
  vid = (v) => {
    window.open(v);
  };
  viddisplay = (v) => {
    const count = this.state.count;
    const removeCount = document.getElementsByClassName("d" + count);
    const newCount = this.state.count + v;
    const addNewCount = document.getElementsByClassName("d" + newCount);
    if (newCount > 0 && newCount <= this.state.res.vid.length) {
      [...removeCount].forEach((item) => {
        item.classList.remove("inline-block");
      });
      [...addNewCount].forEach((item) => {
        item.classList.add("inline-block");
      });
      this.setState({ count: newCount });
    }
  };
  vidtoggle = () => {
    const count = this.state.count;
    const removeCount = document.getElementsByClassName("d" + count);
    if (count < this.state.res.vid.length) {
      [...removeCount].forEach((item) => {
        item.classList.remove("inline-block");
      });
      const newCount = this.state.count + 1;
      const addNewCount = document.getElementsByClassName("d" + newCount);
      [...addNewCount].forEach((item) => {
        item.classList.add("inline-block");
      });
      this.setState({ count: newCount  });
    } else {
      [...removeCount].forEach((item) => {
        item.classList.remove("inline-block");
      });
      const newCount = 1;
      const addNewCount = document.getElementsByClassName("d" + newCount);
      [...addNewCount].forEach((item) => {
        item.classList.add("inline-block");
      });
      this.setState({ count: newCount });
    }
    const video = document.querySelector(".video");
    video.classList.add("flip");
    setTimeout(() => {
      video.classList.remove("flip");
    }, 500);
  };
  handleChange = (selectedOption) => {
    this.setState({ selectedOption, disp: { display: "none" } });
  };
  change = (event) => {
    const nam = event.target.name;
    const val = event.target.value;
    this.setState({ [nam]: val, disp: { display: "none" } });
  };
  submit = (event) => {
    event.preventDefault();
    this.setState({
      a: "Waiting...",
      disp: { display: "block", lineHeight: "50px" },
    });
    if (this.state.name && this.state.email && this.state.phone && this.state.selectedOption && this.state.text) return axios.post('/three', {
      name: this.state.name,
      email: this.state.email,
      phone: this.state.phone,
      selectedOption: this.state.selectedOption,
      text: this.state.text,
    })
      .then((res) => {
        if (res.data.e) return this.setState({
          a: res.data.e,
          disp: { display: "block", lineHeight: "50px" },
        });
        this.setState({
          a: res.data.a,
          disp: { display: "block", lineHeight: "50px" },
          name: null,
          email: null,
          phone: null,
          selectedOption: null,
          text: null,
        });
        document.getElementById("a1").reset();
      })
      .catch((error) => {
        this.setState({
          a: error.response.statusText,
          disp: { display: "block", lineHeight: "50px" },
        });
      });
    this.setState({
      a: "Enquiry from incomplete",
      disp: { display: "block", lineHeight: "50px" },
    });
  };
  render() {
    return (
      <React.Fragment>
        {this.state.load ? (
          <img
            id="load"
            src="https://candid.s3-ap-southeast-2.amazonaws.com/load.gif"
            alt="loading"
          />
        ) : (
          <React.Fragment>
            {this.state.w}
            <section className="welcome">
              <h1 id="welcomeOne">
                <svg
                  id="my-svg"
                  viewBox="0 0 500 500"
                  width="35px"
                  height="35px"
                  xmlns="http://www.w3.org/2000/svg"
                />
                &nbsp;
                {ReactHtmlParser(this.state.res.m1)}
              </h1>
              <h2 id="welcomeTwo" onClick={this.scr}>{ReactHtmlParser(this.state.res.m2)}</h2>
            </section>
            <Slider id="slide" {...this.state.settings}>
              <div>
                <img
                  src={this.state.src1}
                  alt="Slider image 1"
                  width={this.state.wi}
                  height={this.state.hi}
                ></img>
              </div>
              <div>
                <img
                  src={this.state.src0}
                  alt="Slider image 2"
                  width={this.state.wi}
                  height={this.state.hi}
                ></img>
              </div>
            </Slider>
            <section className="video">
              <svg
                onClick={() => this.viddisplay(-1)}
                id="left"
                height="25px"
                viewBox="0 0 500 500"
                width="25px"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="m474.667969 251h-309.335938c-8.832031 0-16-7.167969-16-16s7.167969-16 16-16h309.335938c8.832031 0 16 7.167969 16 16s-7.167969 16-16 16zm0 0" />
                <path d="m250.667969 336.332031c-4.097657 0-8.191407-1.554687-11.308594-4.691406l-85.332031-85.332031c-6.25-6.253906-6.25-16.386719 0-22.636719l85.332031-85.332031c6.25-6.25 16.382813-6.25 22.636719 0 6.25 6.25 6.25 16.382812 0 22.632812l-74.027344 74.027344 74.027344 74.027344c6.25 6.25 6.25 16.382812 0 22.632812-3.136719 3.117188-7.234375 4.671875-11.328125 4.671875zm0 0" />
                <path d="m234.667969 469.667969c-129.386719 0-234.667969-105.28125-234.667969-234.667969s105.28125-234.667969 234.667969-234.667969c97.085937 0 182.804687 58.410157 218.410156 148.824219 3.242187 8.210938-.8125 17.492188-9.023437 20.753906-8.214844 3.203125-17.496094-.789062-20.757813-9.042968-30.742187-78.082032-104.789063-128.535157-188.628906-128.535157-111.746094 0-202.667969 90.925781-202.667969 202.667969s90.921875 202.667969 202.667969 202.667969c83.839843 0 157.886719-50.453125 188.628906-128.511719 3.242187-8.257812 12.523437-12.246094 20.757813-9.046875 8.210937 3.242187 12.265624 12.542969 9.023437 20.757813-35.605469 90.390624-121.324219 148.800781-218.410156 148.800781zm0 0" />
              </svg>
              {this.state.vi}
              <svg
                onClick={() => this.viddisplay(+1)}
                id="right"
                height="25px"
                viewBox="0 0 500 500"
                width="25px"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="m325.332031 251h-309.332031c-8.832031 0-16-7.167969-16-16s7.167969-16 16-16h309.332031c8.832031 0 16 7.167969 16 16s-7.167969 16-16 16zm0 0" />
                <path d="m240 336.332031c-4.097656 0-8.191406-1.554687-11.308594-4.691406-6.25-6.25-6.25-16.382813 0-22.636719l74.027344-74.023437-74.027344-74.027344c-6.25-6.25-6.25-16.386719 0-22.636719 6.253906-6.25 16.386719-6.25 22.636719 0l85.332031 85.335938c6.25 6.25 6.25 16.382812 0 22.632812l-85.332031 85.332032c-3.136719 3.160156-7.230469 4.714843-11.328125 4.714843zm0 0" />
                <path d="m256 469.667969c-97.089844 0-182.804688-58.410157-218.410156-148.824219-3.242188-8.191406.808594-17.492188 9.023437-20.734375 8.191407-3.199219 17.515625.789063 20.757813 9.046875 30.742187 78.058594 104.789062 128.511719 188.628906 128.511719 111.742188 0 202.667969-90.925781 202.667969-202.667969s-90.925781-202.667969-202.667969-202.667969c-83.839844 0-157.886719 50.453125-188.628906 128.511719-3.265625 8.257812-12.566406 12.246094-20.757813 9.046875-8.214843-3.242187-12.265625-12.542969-9.023437-20.734375 35.605468-90.414062 121.320312-148.824219 218.410156-148.824219 129.386719 0 234.667969 105.28125 234.667969 234.667969s-105.28125 234.667969-234.667969 234.667969zm0 0" />
              </svg>
            </section>
            <section className="info">
              <div className="infoOne"></div>
              <div className="infoTwo">
                <h3>
                  {ReactHtmlParser(this.state.res.qh)}
                  <InfoIcon />
                </h3>
                <hr
                  style={{
                    boxShadow: ` 0 5px 5px -5px ${styles.c2}`,
                    border: "7.5px solid transparent",
                  }}
                />
                <p>
                  <q>{ReactHtmlParser(this.state.res.q)}</q>
                </p>
                <hr
                  style={{
                    boxShadow: `0 -5px 5px -5px ${styles.c2}`,
                    border: "7.5px solid transparent",
                  }}
                />
              </div>
            </section>
            <section className="card">
              <div className="cardOne">
                <div className="cardTwo">
                  <span>
                    <h3>{ReactHtmlParser(this.state.res.ch1)}</h3>
                    <img
                      src="https://candid.s3-ap-southeast-2.amazonaws.com/card1.png"
                      alt={ReactHtmlParser(this.state.res.ch1)}
                      width="50"
                      height="50"
                    ></img>
                  </span>
                </div>
                <hr /><p>{ReactHtmlParser(this.state.res.c1)}</p>
                <StarBorderIcon />
                <FilterListIcon />
              </div>
              <div className="cardOne">
                <div className="cardTwo">
                  <span>
                    <h3>{ReactHtmlParser(this.state.res.ch2)}</h3>
                    <img
                      src="https://candid.s3-ap-southeast-2.amazonaws.com/card2.png"
                      alt={ReactHtmlParser(this.state.res.ch2)}
                      width="50"
                      height="50"
                    ></img>
                  </span>
                </div>
                <hr /><p>{ReactHtmlParser(this.state.res.c2)}</p>
                <WhatshotIcon />
                <FilterListIcon />
              </div>
              <div className="cardOne">
                <div className="cardTwo">
                  <span>
                    <h3>{ReactHtmlParser(this.state.res.ch3)}</h3>
                    <img
                      src="https://candid.s3-ap-southeast-2.amazonaws.com/card3.png"
                      alt={ReactHtmlParser(this.state.res.ch3)}
                      width="50"
                      height="50"
                    ></img>
                  </span>
                </div>
                <hr /><p>{ReactHtmlParser(this.state.res.c3)}</p>
                <BuildIcon />
                <FilterListIcon />
              </div>
              <div className="cardOne">
                <div className="cardTwo">
                  <span>
                    <h3>{ReactHtmlParser(this.state.res.ch4)}</h3>
                    <img
                      src="https://candid.s3-ap-southeast-2.amazonaws.com/card4.png"
                      alt={ReactHtmlParser(this.state.res.ch4)}
                      width="50"
                      height="50"
                    ></img>
                  </span>
                </div>
                <hr /><p>{ReactHtmlParser(this.state.res.c4)}</p>
                <a target="4" href={this.state.res.u4}>
                  {ReactHtmlParser(this.state.res.buttons[0])}
                </a>
                <FilterListIcon />
              </div>
              <div className="cardOne">
                <div className="cardTwo">
                  <span>
                    <h3>{ReactHtmlParser(this.state.res.ch5)}</h3>
                    <img
                      src="https://candid.s3-ap-southeast-2.amazonaws.com/card5.png"
                      alt={ReactHtmlParser(this.state.res.ch5)}
                      width="50"
                      height="50"
                    ></img>
                  </span>
                </div>
                <hr /><p>{ReactHtmlParser(this.state.res.c5)}</p>
                <a target="5" href={this.state.res.u5}>
                  {ReactHtmlParser(this.state.res.buttons[0])}
                </a>
                <FilterListIcon />
              </div>
              <div className="cardOne">
                <div className="cardTwo">
                  <span>
                    <h3>{ReactHtmlParser(this.state.res.ch6)}</h3>
                    <img
                      src="https://candid.s3-ap-southeast-2.amazonaws.com/card6.png"
                      alt={ReactHtmlParser(this.state.res.ch6)}
                      width="50"
                      height="50"
                    ></img>
                  </span>
                </div>
                <hr /><p>{ReactHtmlParser(this.state.res.c6)}</p>
                <a target="6" href={this.state.res.u6}>
                  {ReactHtmlParser(this.state.res.buttons[0])}
                </a>
                <FilterListIcon />
              </div>
            </section>
            <section id="enquiry" className="enquiry">
              <div className="headingCont">
                <h3>{ReactHtmlParser(this.state.res.buttons[1])}</h3>
              </div>
              <br></br>
              <form
                id="a1"
                autoComplete="off"
                style={{
                  width: "100%",
                  fontFamily: styles.font3,
                  color: styles.c11,
                }}
                onSubmit={this.submit}
              >
                <label htmlFor="name" className="hiddentext">
                  Name
                </label>
                <TextField
                  id="name"
                  className="MuiFormControl-root-www"
                  InputProps={{
                    style: {
                      color: styles.c3,
                      marginLeft: "2%",
                      backgroundColor: styles.c11,
                      borderRadius: 0,
                      fontSize: "120%",
                      fontWeight: "500",
                    },
                    startAdornment: (
                      <InputAdornment position="start">
                        <AccountCircle />
                      </InputAdornment>
                    ),
                  }}
                  variant="filled"
                  type="text"
                  name="name"
                  placeholder="Name:"
                  onChange={this.change}
                />
                <label htmlFor="email" className="hiddentext">
                  Email
                </label>
                <TextField
                  id="email"
                  className="MuiFormControl-root-www"
                  InputProps={{
                    style: {
                      color: styles.c3,
                      marginLeft: "2%",
                      backgroundColor: styles.c11,
                      borderRadius: 0,
                      fontSize: "120%",
                      fontWeight: "500",
                    },
                    startAdornment: (
                      <InputAdornment position="start">
                        <MailOutlineIcon />
                      </InputAdornment>
                    ),
                  }}
                  variant="filled"
                  type="email"
                  name="email"
                  placeholder="Email:"
                  onChange={this.change}
                />
                <label htmlFor="phone" className="hiddentext">
                  Phone
                </label>
                <Tooltip
                  title="Format: +###############"
                  placement="bottom-end"
                >
                  <TextField
                    id="phone"
                    className="MuiFormControl-root-www"
                    inputProps={{
                      pattern: "[+]?[0-9]{3,15}",
                    }}
                    InputProps={{
                      style: {
                        color: styles.c3,
                        marginLeft: "2%",
                        backgroundColor: styles.c11,
                        borderRadius: 0,
                        fontSize: "120%",
                        fontWeight: "500",
                      },
                      startAdornment: (
                        <InputAdornment position="start">
                          <PhoneIcon />
                        </InputAdornment>
                      ),
                    }}
                    variant="filled"
                    type="tel"
                    name="phone"
                    placeholder="Phone:"
                    onChange={this.change}
                  />
                </Tooltip>
                <div style={{ height: "177px", margin: "50px 0 60px 0" }}>
                  <ListIcon
                    style={{ color: styles.c11, marginLeft: "27.5px" }}
                  />
                  <label htmlFor="select" className="hiddentext">
                    {ReactHtmlParser(this.state.res.buttons[3])}
                  </label>
                  <Select
                    inputId="select"
                    classNamePrefix="react-select"
                    maxMenuHeight={100}
                    value={this.state.selectedOption}
                    onChange={this.handleChange}
                    options={this.state.res.options}
                    styles={customStyles}
                    placeholder={ReactHtmlParser(this.state.res.buttons[3])}
                  />
                </div>
                <SubjectIcon
                  style={{
                    color: styles.c11,
                    marginLeft: "12.5px",
                    marginBottom: "7.5px",
                    display: "block",
                  }}
                />
                <label htmlFor="text" className="hiddentext">
                  Text
                </label>
                <TextField
                  id="text"
                  multiline
                  className="text"
                  minRows="20"
                  fullWidth={true}
                  InputProps={{
                    disableUnderline: true,
                    style: {
                      color: styles.c3,
                      display: "block",
                      width: "100%",
                      height: "150px",
                      backgroundColor: styles.c11,
                      borderRadius: 0,
                      overflowY: "auto",
                      overflowX: "hidden",
                      fontSize: "120%",
                      fontWeight: "500",
                    },
                  }}
                  variant="filled"
                  type="text"
                  name="text"
                  placeholder="Text:"
                  onChange={this.change}
                />
                <Button
                  style={{
                    color: styles.c11,
                    backgroundColor: styles.c3,
                    width: "100%",
                    height: "50px",
                    fontSize: "120%",
                    fontWeight: "400",
                    paddingTop: "10px",
                    borderRadius: 0,
                  }}
                  variant="contained"
                  type="submit"
                >
                  {ReactHtmlParser(this.state.res.buttons[2])}
                </Button>
                <div style={{ height: "50px" }}>
                  <div style={this.state.disp}>
                    <Alertm alert={this.state.a} />
                  </div>
                </div>
              </form>
            </section>
          </React.Fragment>
        )}
      </React.Fragment>
    );
  }
}

export default Home;
